<template>
    <div>
      <div class="head">
        <div @click="select('1')" :class="{ selected : tag==='1'}">
          <span>全部</span>
        </div>
        <div @click="select('2')" :class="{ selected : tag==='2'}">
          <span>待就诊</span>
        </div>
        <div @click="select('3')" :class="{ selected : tag==='3'}">
          <span>已就诊</span>
        </div>
        <div @click="select('4')" :class="{ selected : tag==='4'}">
          <span>已取消</span>
        </div>
      </div>
      <order-card v-for="(item, index) in fliterList" :key="index" :status="item.status" v-on:goDetailPage="goDetailPage"></order-card>
      <div style="height: 20px"></div>
    </div>
</template>

<script>

import orderCard from 'components/orderCard/orderCard'

export default {
  data () {
    return {
      tag: '1',
      orderList: [{status: '1'}, {status: '2'}, {status: '3'}],
      fliterList: []
    }
  },
  methods: {
    fliterListFunc: function (tag) {
      this.fliterList = []
      for (var i = 0; i < this.orderList.length; i++) {
        if (this.orderList[i].status === tag) {
          this.fliterList.push(this.orderList[i])
        }
      }
    },
    select: function (tag) {
      this.tag = tag
      if (tag === '1') {
        this.fliterList = this.orderList
      } else if (tag === '2') {
        this.fliterListFunc('1')
      } else if (tag === '3') {
        this.fliterListFunc('2')
      } else if (tag === '4') {
        this.fliterListFunc('3')
      }
    },
    goDetailPage: function (status) {
      if (status === '1') {
        this.$router.push({
          path: `/orderReady`
        })
      } else if (status === '2') {
        this.$router.push({
          path: `/orderFinish`
        })
      } else if (status === '3') {
        this.$router.push({
          path: `/cancelVerb`
        })
      }
    }
  },
  created: function () {
    this.fliterList = this.orderList
  },
  components: {
    orderCard
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

.head
    @extend .flex
    @extend .flex-row
    @extend .flex-around
    height 60px
    background-color white
    color black

.selected
    color white
    background-color $color-theme-blue
    height 30px
    border-radius 15px
    span
        line-height 30px
        padding 10px
</style>


