<template>
    <div>
      <div class="block">
        <block-head :title="'待就诊'" v-if="status==='1'"></block-head>
        <block-head :title="'已就诊'" v-if="status==='2'"></block-head>
        <block-head :title="'已取消'" v-if="status==='3'"></block-head>
        <div class="verbTime" :class="{ isActive : status!=='1'}">
          <span>预约时间:</span>
          <span>2017/12/12 星期六 14:20</span>
          <hr v-if="status==='3'">
        </div>
        <p v-if="status==='1'">注: 请提前10分钟内到达门店等候服务</p>
        <dotted-line></dotted-line>
        <info-row :left="'预约服务'" :right="'朱氏头皮针'"></info-row>
        <info-row :left="'服务价格'" :right="'200'"></info-row>
        <dotted-line></dotted-line>
        <div class="orderDetail" @click="orderDetail">
          <span>订单详情</span>
        </div>
      </div>
    </div>
</template>

<script>

import blockHead from 'components/blockHead/blockHead'
import dottedLine from 'components/dottedLine/dottedLine'
import infoRow from 'components/infoRow/infoRow'

export default {
  data () {
    return {
    }
  },
  components: {
    blockHead,
    dottedLine,
    infoRow
  },
  props: {
    status: {
      type: String,
      default: ''
    }
  },
  methods: {
    orderDetail: function () {
      this.$emit('goDetailPage', this.status)
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

.block
    background-color white
    margin 10px 10px 0px 10px
    padding-bottom 20px
    p
        color $color-theme-gray_1
        font-size $font-size-level_0
        text-align right
        padding-right 10px
    .verbTime
        @extend .flex
        @extend .flex-row
        @extend .flex-start
        position relative
        hr
            position absolute
            width 93%
            margin-left 15px
            top 22px
        span
            line-height 60px
            font-weight $font-weight-level_7
        span:first-of-type
            width 100px
        span:last-of-type
            flex-grow 2
            text-align right
            padding-right 10px

.orderDetail
    background-color $color-theme-blue
    width 90px
    height 36px
    color white
    font-size $font-size-level_2
    border-radius 3px
    margin-left 70%
    span
        line-height 36px

.isActive
  color $color-theme-gray_1

</style>


